import React from "react";
import { Button, Container, Row, Dropdown, Col } from "react-bootstrap";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import "./LandingStyles.css";

function LandingPage() {
  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  return (
    <div className="main">
      <Container>
        <Row className="justify-content-md-center">
          <div className="intro-text">
            <div>
              <h1 className="title"> </h1>
              <p className="subtitle">
                        <br />
                  
              </p>
            </div>
          </div>
          <div className="buttonContainer">
            {userInfo ? (
              <>
                <Container>
                  <Row>
                    <Col>
                      <Dropdown>
                        <Dropdown.Toggle variant="success" id="dropdown-basic">
                          Covid Info
                        </Dropdown.Toggle>

                        <Dropdown.Menu>
                          <Dropdown.Item href="/covid-total">
                               
                          </Dropdown.Item>
                          <Dropdown.Item href="/covid-occur">
                              
                          </Dropdown.Item>
                          <Dropdown.Item href="/covid-gender">
                            ,  
                          </Dropdown.Item>
                          <Dropdown.Item href="/covid-hospital">
                              
                          </Dropdown.Item>
                        </Dropdown.Menu>
                      </Dropdown>
                    </Col>
                    <Col>
                      <Link to="/board">
                        <Button
                          variant="outline-primary"
                          size="sm"
                          className="landingbutton"
                        >
                          
                        </Button>
                      </Link>
                    </Col>
                  </Row>
                </Container>
              </>
            ) : (
              <Container>
                <Row>
                  <Col>
                    <Dropdown>
                      <Dropdown.Toggle variant="success" id="dropdown-basic">
                        Covid Info
                      </Dropdown.Toggle>

                      <Dropdown.Menu>
                        <Dropdown.Item href="/covid-total">
                             
                        </Dropdown.Item>
                        <Dropdown.Item href="/covid-occur">
                            
                        </Dropdown.Item>
                        <Dropdown.Item href="/covid-gender">
                          ,  
                        </Dropdown.Item>
                      </Dropdown.Menu>
                    </Dropdown>
                  </Col>
                  <Col>
                    <Link to="/login" size="sm">
                      <Button size="sm" className="landingbutton">
                        Login
                      </Button>
                    </Link>
                  </Col>
                  <Col>
                    <Link to="/register">
                      <Button
                        variant="outline-primary"
                        size="sm"
                        className="landingbutton"
                      >
                        Sign Up
                      </Button>
                    </Link>
                  </Col>
                </Row>
              </Container>
            )}
          </div>
        </Row>
      </Container>
    </div>
  );
}

export default LandingPage;
