import React, { useEffect, useState } from "react";
import {
  Container,
  Row,
  Col,
  Card,
  Pagination,
  ListGroup,
} from "react-bootstrap";
import { Map, MapMarker, useMap } from "react-kakao-maps-sdk";
import axios from "axios";
import Loading from "../../components/Loading";

function CovidHospital() {
  const [state, setState] = useState({
    center: {
      lat: 33.450701,
      lng: 126.570667,
    },
    errMsg: null,
    isLoading: true,
  });
  const [dt, setDt] = useState([]);
  const [loading, setLoading] = useState(false);
  const [active, setActive] = useState(1);
  const [positions, setPositions] = useState([]);
  const pageItems = [];
  for (let i = 1; i <= 10; i++) {
    pageItems.push(
      <Pagination.Item
        key={i}
        onClick={(e) => {
          setActive(parseInt(e.target.text));
        }}
      >
        {i}
      </Pagination.Item>
    );
  }

  useEffect(() => {
    if (navigator.geolocation) {
      // GeoLocation    
      navigator.geolocation.getCurrentPosition(
        (position) => {
          setState((prev) => ({
            ...prev,
            center: {
              lat: position.coords.latitude, // 
              lng: position.coords.longitude, // 
            },
            isLoading: false,
          }));
        },
        (err) => {
          setState((prev) => ({
            ...prev,
            errMsg: err.message,
            isLoading: false,
          }));
        }
      );
    } else {
      // HTML5 GeoLocation         
      setState((prev) => ({
        ...prev,
        errMsg: "geolocation  ..",
        isLoading: false,
      }));
    }
    setLoading(true);
    axios({
      method: "get",
      url: `/api/covid-info/hospital`,
    })
      .then((res) => {
        let newPositions = [];
        res.data.item.forEach((each) => {
          const position = {
            title: each.yadmNm,
            latlng: {
              lat: Number(each.YPosWgs84),
              lng: Number(each.XPosWgs84),
            },
          };
          newPositions.push(position);
        });
        setPositions(newPositions);
        setDt(res.data.item);
        setLoading(false);
      })
      .catch((err) => console.log(err));
  }, []);

  const EventMarkerContainer = ({ position, content }) => {
    const map = useMap();
    const [isVisible, setIsVisible] = useState(false);

    return (
      <MapMarker
        position={position} //   
        // @ts-ignore
        onClick={(marker) => map.panTo(marker.getPosition())}
        onMouseOver={() => setIsVisible(true)}
        onMouseOut={() => setIsVisible(false)}
      >
        {isVisible && content}
      </MapMarker>
    );
  };
  return (
    <Container style={{ height: "100%", weight: "100vw", overflow: "hidden" }}>
      <Row
        style={{
          height: "100%",
          weight: "100%",
          margin: "10px",
        }}
      >
        <Map
          center={state.center}
          style={{ width: "50%", height: "72vh", overflow: "hidden" }}
        >
          {!state.isLoading && (
            <MapMarker position={state.center}>
              <div style={{ padding: "5px", color: "#000" }}>
                {state.errMsg ? state.errMsg : " ?!"}
              </div>
            </MapMarker>
          )}
          {positions &&
            positions.map((position, index) => (
              <EventMarkerContainer
                key={`${position.title}-${position.latlng}`}
                position={position.latlng} //   
                image={{
                  src: "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/markerStar.png", //  
                  size: {
                    width: 24,
                    height: 35,
                  }, //  
                }}
                title={position.title} //  ,     
              />
            ))}
        </Map>
        <Col>
          <Container>
            {loading ? (
              <Loading></Loading>
            ) : (
              <Row xs={1} md={2} className="g-4">
                {dt.slice(active * 4, (active + 1) * 4).map((each, idx) => (
                  <Col key={each.XPos} style={{ padding: "10px" }}>
                    <Card border="primary">
                      <Card.Title
                        style={{
                          display: "flex",
                          justifyContent: "center",
                          alignItems: "center",
                        }}
                      >
                        {each.yadmNm}
                      </Card.Title>
                      <ListGroup variant="flush">
                        <ListGroup.Item> : {each.addr}</ListGroup.Item>
                        <ListGroup.Item> : {each.telno}</ListGroup.Item>
                        <ListGroup.Item>
                           : {each.mgtStaDd}
                        </ListGroup.Item>
                        <ListGroup.Item>
                          <Card.Link
                            href={`https://map.kakao.com/link/map/${each.yadmNm},${each.YPosWgs84},${each.XPosWgs84}`}
                            target="_blank"
                          >
                             
                          </Card.Link>
                        </ListGroup.Item>
                      </ListGroup>
                    </Card>
                  </Col>
                ))}
                <Pagination style={{ alignSelf: "center" }}>
                  {pageItems}
                </Pagination>
              </Row>
            )}
          </Container>
        </Col>
      </Row>
    </Container>
  );
}

export default CovidHospital;
